
.headerH1{
    color: white;
    font-family: 'Uchen', serif;
    font-weight: 100;
    font-size: 40px;
    user-select: none;
}



.header__position{
    height: auto;
    width: auto;
    min-height: 100px;
    min-width: 100%;
    padding-top: 10px;
    background-color: transparent;
    position: absolute;
}
.header__position:hover{
    cursor: pointer;
    transition: 0.5s;
}
.header__flex{
    display: flex;
}
.header__flex1{
    display: inline-block;
    margin-left: 750px;
    margin-top: 30px;
}
.header__flex2{
    display: inline-block;
    margin-top: 30px;
    margin-left: 30px;
}
.header__flex3{
    display: inline-block;
    margin-top: 30px;
    margin-left: 30px;
}
.header__flex4{
    display: inline-block;
    margin-top: 30px;
    margin-left: 30px;
}